"use client";

import { useState, useEffect } from "react";
import dynamic from "next/dynamic";

const Select = dynamic(() => import("react-select"), { ssr: false });

interface Booking {
    id: number;
    team: string;
    customerGroup: string;
    customerName: string;
    price: number; // แก้ไขให้เป็น number
    dailyQuantities: Record<string, number>;
    fishSize: string;
    fishType: string;
  }
  
  interface FormData {
    team: string;
    customerGroup: string;
    customerName: string;
    fishSize: string;
    fishType: string;
    price: number; // ใช้ number สำหรับราคาทั้งสัปดาห์
    dailyQuantities: Record<string, number>;
  }
  

interface BookingFormProps {
  setBookings: React.Dispatch<React.SetStateAction<Booking[]>>;
  editingBooking?: Booking | null;
  clearEditingBooking?: () => void;
}

const BookingForm: React.FC<BookingFormProps> = ({
  setBookings,
  editingBooking,
  clearEditingBooking,
}) => {
    const getWeekDays = (): string[] => {
        const startDate = new Date();
        const days: string[] = [];
      
        // หา dayOffset สำหรับวันจันทร์ (1) ถึงวันเสาร์ (6)
        for (let i = 1; i <= 6; i++) {
          const date = new Date(startDate);
          date.setDate(startDate.getDate() + (i - startDate.getDay()));
          days.push(date.toISOString().split("T")[0]); // แปลงวันที่เป็น YYYY-MM-DD
        }
      
        return days;
      };
      

  const fishSizeOptions = [
    { value: "3/4", label: "3/4" },
    { value: "4/5", label: "4/5" },
    { value: "5/6", label: "5/6" },
  ];

  const fishTypeOptions = [
    { value: "Norway", label: "Norway" },
    { value: "Trout", label: "Trout" },
  ];

  const [formData, setFormData] = useState<FormData>({
    team: "",
    customerGroup: "",
    customerName: "",
    fishSize: "Medium",
    fishType: "Tilapia",
    price: 0,
    dailyQuantities: {},
  });

  const [weekPrice, setWeekPrice] = useState<number>(0); // State for weekly price

  useEffect(() => {
    if (editingBooking) {
      setFormData(editingBooking); // โหลดข้อมูลเมื่อแก้ไข
    } else {
      const weekDays = getWeekDays(); // ดึงวันจันทร์ถึงวันเสาร์
      setFormData({
        team: "",
        customerGroup: "",
        customerName: "",
        fishSize: "",
        fishType: "",
        price: 0,
        dailyQuantities: weekDays.reduce((acc, day) => ({ ...acc, [day]: 0 }), {}),
      });
    }
  }, [editingBooking]);
  

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    if (name.startsWith("day-")) {
      const day = name.replace("day-", "");
      setFormData((prev) => ({
        ...prev,
        dailyQuantities: { ...prev.dailyQuantities, [day]: Number(value) },
      }));
    } else {
      // ใช้งาน price เป็น number โดยตรง
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };
  

  const handleSelectChange = (field: "fishSize" | "fishType", option: any) => {
    setFormData((prev) => ({ ...prev, [field]: option.value }));
  };

//   const applyWeekPrice = () => {
//     const weekDays = getWeekDays();
//     setFormData((prev) => ({
//       ...prev,
//       price: weekPrice, // ใช้ price เป็นตัวเลขเดียว
//       dailyQuantities: weekDays.reduce((acc, day) => ({ ...acc, [day]: weekPrice }), {}),
//     }));
//   };
  

const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
  
    // ตรวจสอบข้อมูลก่อนส่ง
    if (
      !formData.team ||
      !formData.customerGroup ||
      !formData.customerName ||
      !formData.price ||
      Object.keys(formData.dailyQuantities).length === 0
    ) {
      alert("Please fill in all required fields.");
      return;
    }
  
    try {
      const method = editingBooking ? "PUT" : "POST";
      const url = editingBooking ? `/api/bookings/${editingBooking.id}` : "/api/bookings";
  
      const response = await fetch(url, {
        method,
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
  
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({ error: "Unknown server error" }));
        throw new Error(errorData.error || "Failed to save booking");
      }
  
      const updatedBooking = await response.json();
  
      if (editingBooking) {
        setBookings((prev) =>
          prev.map((booking) =>
            booking.id === updatedBooking.id ? updatedBooking : booking
          )
        );
        alert("Booking updated!");
      } else {
        setBookings((prev) => [...prev, updatedBooking]);
        alert("Booking saved!");
      }
  
      clearEditingBooking?.();
    } catch (error) {
      console.error("Error saving booking:", error);
      alert("Error saving booking.");
    }
  };
  

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <h1 className="text-xl font-bold">
        {editingBooking ? "Edit Booking" : "Add New Booking"}
      </h1>
      <div>
        <label>Team:</label>
        <input
          type="text"
          name="team"
          value={formData.team}
          onChange={handleChange}
          className="border rounded px-2 py-1"
        />
      </div>
      <div>
        <label>Customer Group:</label>
        <input
          type="text"
          name="customerGroup"
          value={formData.customerGroup}
          onChange={handleChange}
          className="border rounded px-2 py-1"
        />
      </div>
      <div>
        <label>Customer Name:</label>
        <input
          type="text"
          name="customerName"
          value={formData.customerName}
          onChange={handleChange}
          className="border rounded px-2 py-1"
        />
      </div>
      <div>
        <label>Fish Size:</label>
        <Select
          options={fishSizeOptions}
          value={fishSizeOptions.find((opt) => opt.value === formData.fishSize)}
          onChange={(option) => handleSelectChange("fishSize", option)}
        />
      </div>
      <div>
        <label>Fish Type:</label>
        <Select
          options={fishTypeOptions}
          value={fishTypeOptions.find((opt) => opt.value === formData.fishType)}
          onChange={(option) => handleSelectChange("fishType", option)}
        />
      </div>

      {/* Weekly Price Input */}
      <div>
        <label>Price for Entire Week:</label>
        <div className="flex items-center space-x-4">
          <input
            type="number"
            name="weekPrice"
            value={weekPrice}
            onChange={(e) => setWeekPrice(Number(e.target.value))}
            className="border rounded px-2 py-1"
          />
          {/* <button
            type="button"
            onClick={applyWeekPrice}
            className="bg-green-500 text-white px-4 py-2 rounded"
          >
            Apply to All Days
          </button> */}
        </div>
      </div>

      {/* Daily Prices */}
      <h2 className="text-lg font-bold mt-6">Daily Quantities</h2>
      {Object.keys(formData.dailyQuantities).map((day) => (
        <div key={day} className="grid grid-cols-2 gap-4 items-center">
          <div>
            <label>{day}:</label>
            <input
              type="number"
              name={`day-${day}`}
              value={formData.dailyQuantities[day]}
              onChange={handleChange}
              className="border rounded px-2 py-1"
            />
          </div>
        </div>
      ))}

      <div className="flex space-x-4">
        <button
          type="submit"
          className={`px-4 py-2 rounded ${
            editingBooking
              ? "bg-yellow-500 text-white"
              : "bg-blue-500 text-white"
          }`}
        >
          {editingBooking ? "Update" : "Save"}
        </button>
        {editingBooking && (
          <button
            type="button"
            onClick={clearEditingBooking}
            className="bg-gray-500 text-white px-4 py-2 rounded"
          >
            Cancel
          </button>
        )}
      </div>
    </form>
  );
};

export default BookingForm;
